
/* ===[ MAIN.JSX ]======================================= */

body{
    background-color: rgba(153, 235, 255, 0.925);
    text-align: center;
    padding: 30px 100px;
}

.MainContainer{
    background-color: rgba(254, 254, 254, 0.782);
    border: 7px solid white;
    padding: 15px 50px;
    -webkit-box-shadow: 5px 5px 5px #888888;
            box-shadow: 5px 5px 5px #888888;
    border-radius: 10px; 
}

.menu1{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.menuButton{
    margin: 2px;
    padding: 3px 6px;
    border: 1px solid black;
    -webkit-box-shadow: 5px 5px 5px #888888;
            box-shadow: 5px 5px 5px #888888;
    border-radius: 10px; 
    background-color: rgba(152, 205, 255, 0.775);
}

.menuButton:hover{
    background-color: rgba(170, 252, 180, 0.823);
}


